import util from '@/libs/util.js'
import { AccountLogin } from '@/api/sys/login'

export default {
  namespaced: true,
  actions: {
    /**
     * @description 
     * @param {Object} param context
     * @param {Object} param vm {Object} vue 
     * @param {Object} param username {String} 
     * @param {Object} param password {String} 
     * @param {Object} param route {Object} 
     */
    login ({ dispatch }, {
      vm,
      username,
      password,
      route = {
        name: 'index'
      }
    }) {
      // 
      AccountLogin({
        email: username,
        password
      })
        .then(async res => {
          if (res.data.code > 0) {
            if (res.data.msg.detail) {
              vm.$message.error(res.data.msg.detail[0].field + " " + res.data.msg.detail[0].message);
            } else {
              vm.$message.error(res.data.msg);
            }
          } else {
            let userinfo = res.data.data.userInfo;
            let token = res.data.data.token;
            //  cookie  uuid  token  cookie
            // 
            // uuid    
            // token   token
            //  token 
            util.cookies.set('uuid', userinfo._id)
            util.cookies.set('token', token)
            //  vuex 
            await dispatch('d2admin/user/set', {
              name: userinfo.nickname
            }, { root: true })
            // 
            await dispatch('load')
            //   cookie 
            const path = util.cookies.get('redirect')
            // 
            vm.$router.replace(path ? { path } : route)
            //  cookie 
            util.cookies.remove('redirect')
          }
        })
        .catch(err => {
          console.log('err: ', err)
        })
    },
    /**
     * @description 
     * @param {Object} param context
     * @param {Object} param vm {Object} vue 
     * @param {Object} param confirm {Boolean} 
     */
    logout({ commit }, { vm, confirm = false }) {
      /**
       * @description 
       */
      function logout() {
        // cookie
        util.cookies.remove('token')
        util.cookies.remove('uuid')
        // 
        vm.$router.push({
          name: 'login'
        })
      }
      // 
      if (confirm) {
        commit('d2admin/gray/set', true, { root: true })
        vm.$confirm('?  ', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
          .then(() => {
            commit('d2admin/gray/set', false, { root: true })
            logout()
          })
          .catch(() => {
            commit('d2admin/gray/set', false, { root: true })
            vm.$message('')
          })
      } else {
        logout()
      }
    },
    /**
     * @description 
     * @param {Object} state vuex state
     */
    load({ commit, dispatch }) {
      return new Promise(async resolve => {
        // DB -> store 
        await dispatch('d2admin/user/load', null, { root: true })
        // DB -> store 
        await dispatch('d2admin/theme/load', null, { root: true })
        // DB -> store 
        await dispatch('d2admin/transition/load', null, { root: true })
        // DB -> store 
        await dispatch('d2admin/page/openedLoad', null, { root: true })
        // DB -> store 
        await dispatch('d2admin/menu/asideCollapseLoad', null, { root: true })
        // DB -> store 
        await dispatch('d2admin/size/load', null, { root: true })
        // end
        resolve()
      })
    }
  }
}
